
.giveRecord {
  min-height: 100vh;
  background: #f6f6f6;
  .contentBox {
    display: flex;
    justify-content: center;
    .content {
      width: 100%;
      display: flex;
      justify-content: center;
      margin: 5px 0 0;
      .information {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        overflow-y: scroll;
        -webkit-overflow-scrolling: touch;
        .container {
          width: 100%;
          display: flex;
          justify-content: space-between;
          background: #fff;
          border-bottom: 1px solid #f6f7f6;
          padding: 10px 0;
          .container-left {
            display: flex;
            flex-direction: column;
            margin: 0 0 0 16px;
            .info {
              display: flex;
              .give,
              .to {
                font-size: 15px;
                font-family: PingFang SC-中等, PingFang SC;
                font-weight: normal;
                color: #333333;
                line-height: 14px;
              }
              .message {
                font-size: 15px;
                font-family: PingFang SC-中等, PingFang SC;
                font-weight: normal;
                color: #ff2a00;
                line-height: 14px;
              }
              .title {
                width: 1.82rem;
                font-size: 15px;
                font-family: PingFang SC-中等, PingFang SC;
                font-weight: normal;
                color: #333333;
                line-height: 14px;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
              }
            }
            .date {
              display: flex;
              margin: 7px 0 0;
              .time {
                font-size: 12px;
                font-family: PingFang SC-中等, PingFang SC;
                font-weight: normal;
                color: #999999;
                line-height: 14px;
              }
            }
          }
          .container-right {
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            margin: 0 16px 0 0;
            .number {
              font-size: 14px;
              font-family: PingFang SC-中等, PingFang SC;
              font-weight: normal;
              color: #ff2a00;
              line-height: 14px;
            }
            .operator {
              font-size: 12px;
              font-family: PingFang SC-中等, PingFang SC;
              font-weight: normal;
              color: #999999;
              line-height: 12px;
              margin: 8px 0 0;
            }
          }
        }
      }
      .information::-webkit-scrollbar {
        display: none;
      }
    }
  }
}
::v-deep .van-nav-bar__content {
  height: 0.53rem;
}
::v-deep .van-nav-bar__title {
  font-size: 17px;
  font-family: PingFang SC-中等, PingFang SC;
  font-weight: normal;
  color: #000000;
  line-height: 17px;
}
